<template>
  <div class="ratingselect">
    <div class="select-type border-1px">
      <span class="block all" @click="select(2,$event)" :class="{'active':selectType===2}">{{desc.ALL}}<span
        class="count">{{ratings.length}}</span></span>
      <span class="block posi" @click="select(0,$event)" :class=" {'active':selectType===0}">{{desc.POSITIVE}}<span
        class="count">{{positives.length}}</span></span>
      <span class="block nega" @click="select(1,$event)" :class=" {'active':selectType===1}">{{desc.NEGATIVE}}<span
        class="count">{{nagatives.length}}</span></span>
    </div>
    <div class="switch border-1px" @click="toggle($event)">
      <span class="icon-check_circle icon" :class="{'on':onlyContent}"></span>
      <span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  const POSITIVE = 0;
  const NEGATIVE = 1;
  const ALL = 2;
  export default {
    props: {
      ratings: {
        type: Array,
        default () {
          return [];
        }
      },
      selectType: {
        type: Number,
        default: ALL
      },
      onlyContent: {
        type: Boolean,
        default: false
      },
      desc: {
        type: Object,
        default () {
          return {
            ALL: '全部',
            POSITIVE: '满意',
            NEGATIVE: '不满意'
          };
        }
      }
    },
    methods: {
      select (type, event) {
        if (!event._constructed) {
          return;
        }
        this.$emit('selecttype', type);
      },
      toggle (event) {
        if (!event._constructed) {
          return;
        }
        this.$emit('toggle');
      }
    },
    computed: {
      positives () {
        return this.ratings.filter((rating) => {
          return rating.rateType === POSITIVE;
        });
      },
      nagatives () {
        return this.ratings.filter((rating) => {
          return rating.rateType === NEGATIVE;
        });
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin.styl'
  .ratingselect
    .select-type
      margin: 0 18px
      padding: 18px 0
      font-size: 0
      border-1px(rgba(7, 17, 27, 0.1))
      .block
        display: inline-block
        padding: 8px 12px
        margin-right: 8px
        border-radius: 2px
        font-size: 12px
        line-height: 16px
        color: rgb(77, 85, 93)
        &.active
          color: #fff
        .count
          margin-left: 2px
          font-size: 8px
        &.all
          background: rgba(0, 160, 220, 0.2)
          &.active
            background: rgb(0, 160, 220)
        &.posi
          background: rgba(0, 160, 220, 0.2)
          &.active
            background: rgb(0, 160, 220)
        &.nega
          background: rgba(77, 85, 93, 0.2)
          &.active
            background: rgb(77, 85, 93)
    .switch
      padding: 12px 18px
      line-height: 24px
      color: rgb(147, 153, 159)
      border-1px(rgba(7, 17, 27, 0.1))
      .icon
        display: inline-block
        vertical-align: top
        margin-right: 4px
        font-size: 24px
        &.on
          color: #00c850
      .text
        display: inline-block
        vertical-align: top
        font-size: 12px
</style>



