<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuwrapper">
      <ul>
        <li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}"
            @click="selectMenu(index,$event)">
          <span class="text border-1px">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodwrapper">
      <ul>
        <li v-for="item in goods" class="food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click.stop="selected(food,$event)" v-for="food in item.foods" class="food-item border-1px">
              <div class="icon1">
                <img :src="food.icon" class="ic">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="sp1">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  ￥<span class="pri1">{{food.price}}</span><span class="pri2"
                                                                 v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food" :selectFood="selectFood" v-on:cartadd="other"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <goodcart ref="goodcart" :selectFood="selectFood" :deliveryPrice="seller.deliveryPrice"
              :min-price="seller.minPrice"></goodcart>
    <food :food="selectedFood" @add="other" ref="food"></food>
  </div>
</template>

<script type="text/ecmascript-6">
  import Bscroll from 'better-scroll';
  import goodcart from 'components/goodcart/goodcart';
  import cartcontrol from 'components/cartcontrol/cartcontrol';
  import food from 'components/food/food';

  const ERR_OK = 0;
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        a: '',
        goods: [],
        listHeight: [],
        scrollY: 0,
        selectedFood: {}
      };
    },
    components: {
      goodcart,
      cartcontrol,
      food
    },
    computed: {
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0;
      },
      selectFood () {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        return foods;
      }
    },
    created () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      this.$http.get('/api/goods').then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.goods = response.data;
          this.$nextTick(() => {
            this._initScroll();
            this._calculateHeight();
          });
        }
      });
    },
    methods: {
      _initScroll () {
        this.menuScroll = new Bscroll(this.$refs.menuwrapper, {
          click: true
        });
        this.foodScroll = new Bscroll(this.$refs.foodwrapper, {
          click: true,
          probeType: 3
        });
        this.foodScroll.on('scroll', (pos) => {
          this.scrollY = Math.abs(Math.round(pos.y));
        });
      },
      _calculateHeight () {
        let height = 0;
        this.listHeight.push(height);
        let foodItems = this.$refs.foodwrapper.getElementsByClassName('food-list-hook');
        for (let i = 0; i < foodItems.length; i++) {
          let item = foodItems[i];
          height += item.clientHeight;
          this.listHeight.push(height);
        }
      },
      selectMenu (index, event) {
        if (!event._constructed) {
          return;
        }
        let foodItems = this.$refs.foodwrapper.getElementsByClassName('food-list-hook');
        let el = foodItems[index];
        this.foodScroll.scrollToElement(el, 300);
      },
      other: function (a) {
        // 体验优化 异步执行
        this.$nextTick(() => {
          this.$refs.goodcart.drop(a);
        });
      },
      selected (food, event) {
        if (!event._constructed) {
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .goods
    display: flex
    position: absolute
    top: 174px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      width: 80px
      background-color: #f3f5f7
      .menu-item
        display: table
        height: 54px
        width: 56px
        padding: 0 12px
        line-height: 14px
        &.current
          position: relative
          z-index: 10
          margin-top: -1px
          background: #fff
          font-weight: 700
          color: rgb(240, 20, 20)
          .text
            border-none()
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.guarantee
            bg-image('guarantee_3')
          &.invoice
            bg-image('invoice_3')
          &.special
            bg-image('special_3')
        .text
          display: table-cell
          height: 54px
          width: 56px
          vertical-align: middle
          border-1px(rgba(7, 17, 27, .1))
          font-size: 12px

    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left: 2px solid #d9ddel
        font-size: 12px
        color: rgb(147, 153, 157)
        background-color: #f3f5f7
        text-indent: 14px
      .food-item
        display: flex
        margin: 18px 18px 0 18px
        padding-bottom: 18px
        border-1px(rgba(7, 17, 27, .1))
        &:last-child
          border-none()
        .icon1
          flex: 0 0 38px
          margin-right: 10px
          .ic
            width: 57px
        .content
          flex: 1
          .name
            margin: 2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size: 14px
            color: rgb(7, 17, 27)
          .desc
            margin: 0 18px 8px 0
            height: 10px
            line-height: 10px
            font-size: 10px
            color: rgb(147, 153, 159)
          .extra
            margin: 0 18px 8px 0
            height: 10px
            line-height: 12px
            font-size: 10px
            color: rgb(147, 153, 159)
            .sp1
              margin-right: 12px
          .price
            margin: 0 18px 18px 0
            line-height: 14px
            font-size: 10px
            font-weight: normal
            color: rgb(240, 20, 20)
            .pri1
              margin-right: 8px
              font-size: 14px
              font-weight: 700
            .pri2
              text-decoration: line-through
              color: rgb(147, 153, 159)
          .cartcontrol-wrapper
            position: absolute
            z-index: 700
            right: 0
            bottom: 12px
</style>



