<template>
  <div class="seller-wrapper" ref="rating">
    <div class="seller-content">
      <div class="overview">
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title">综合评价</div>
          <div class="rank">高于周边商家{{seller.rankRate}}</div>
        </div>
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="servies">服务态度</span>
            <star :size="36" :score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="servies">商品评分</span>
            <star :size="36" :score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="deliveryTime">
            <span class="time">送达时间</span>
            <span class="time1">{{seller.deliveryTime}}分钟</span>
          </div>
        </div>
      </div>
      <split></split>
      <ratingselect @toggle="toggle" @selecttype="select" :selectType="selectType" :onlyContent="onlyContent"
                    :ratings="rating"></ratingselect>
      <div class="ratting-wrapper">
        <ul>
          <li class="item-content border-1px" v-for="item in rating" v-show="show(item.rateType, item.text)">
            <div class="avatar">
              <img class="img-avatar" :src="item.avatar">
            </div>
            <div class="content">
              <h1 class="username">{{item.username}}</h1>
              <div class="star-content">
                <star :size="24" :score="item.score"></star>
                <span class="time" v-show="item.deliveryTime">{{item.deliveryTime}}分钟送达</span>
              </div>
              <p class="text">{{item.text}}</p>
              <div class="recommend" v-show="item.recommend.length!==0">
                <span :class="{'icon-thumb_up':item.recommend!==0}"></span>
                <span class="rcm" v-show="index<3" v-for="(rcm,index) in item.recommend">{{rcm}}</span>
              </div>
            </div>
            <div class="time1">{{item.rateTime | formatData}}</div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BSroll from 'better-scroll';
  import star from 'components/star/star';
  import split from 'components/split/split';
  import ratingselect from 'components/ratingselect/ratingselect';
  import { formatData } from 'common/js/data';

  const ERR_OK = 0;
  const ALL = 2;
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        selectType: ALL,
        onlyContent: true,
        rating: []
      };
    },
    methods: {
      show (type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return this.selectType === type;
        }
      },
      select (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggle () {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      }
    },
    filters: {
      formatData (time) {
        let date = new Date(time);
        return formatData(date, 'yyyy-MM-dd hh:mm');
      }
    },
    created () {
      this.$http.get('/api/ratings').then((response) => {
        response = response.body;
        if (response.errno === ERR_OK) {
          this.rating = response.data;
          this.$nextTick(() => {
            this.scroll = new BSroll(this.$refs.rating, {
              click: true
            });
          });
        }
        ;
      });
    },
    components: {
      star,
      split,
      ratingselect
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  .seller-wrapper
    position: absolute
    top: 174px
    left: 0
    bottom: 0
    width: 100%
    overflow: hidden
    .overview
      display: flex
      padding: 18px 0
      .overview-left
        flex: 0 0 137px
        width: 137px
        text-align: center
        border-right: 1px solid rgba(7, 17, 27, 0.1)
        @media screen and (max-width: 320px)
          flex: 0 0 120px
          width: 120px
        .score
          margin-bottom: 6px
          font-size: 24px
          line-height: 28px
          color: rgb(255, 153, 0)
        .title
          margin-bottom: 8px
          font-size: 12px
          line-height: 12px
          color: rgb(7, 17, 27)
        .rank
          font-size: 10px
          line-height: 10px
          color: rgb(147, 153, 159)
      .overview-right
        flex: 1
        padding-left: 24px
        @media screen and (max-width: 320px)
          padding-left: 6px
        .score-wrapper
          margin-bottom: 8px
          font-size: 0
          .servies
            display: inline-block
            vertical-align: top
            font-size: 12px
            line-height: 18px
            color: rgb(7, 17, 27)
            display: inline-block
            vertical-align: top
            line-height: 18px
          .star
            display: inline-block
            vertical-align: top
            margin: 0 12px
          .score
            display: inline-block
            vertical-align: top
            line-height: 18px
            font-size: 12px
            color: rgb(255, 153, 0)
        .deliveryTime
          line-height: 18px
          font-size: 0
          .time
            margin-right: 12px
            display: inline-block
            font-size: 12px
            color: rgb(7, 17, 27)
          .time1
            display: inline-block
            font-size: 12px
            color: rgb(147, 153, 159)
    .ratting-wrapper
      padding: 0 18px
      border-top: 1px solid rgba(7, 17, 27, 0.1)
      .item-content
        display: flex
        display: relative
        border-1px(rgba(7, 17, 27, 0.1))
        padding: 18px 0
        .avatar
          flex: 0 0 28px
          margin-right: 12px
          .img-avatar
            width: 28px
            height: 28px
            border-radius: 50%
        .content
          flex: 1
          .username
            font-size: 10px
            line-height: 12px
            color: rgb(7, 17, 27)
          .star-content
            margin: 4px 0 6px 0
            font-size: 0
            .star
              display: inline-block
              vertical-align: top
              margin-right: 6px
            .time
              display: inline-block
              vertical-align: top
              font-size: 10px
              line-height: 12px
              font-weight: 200
              color: rgb(143, 157, 159)
          .text
            font-size: 12px
            line-height: 18px
            color: rgb(7, 17, 27)
          .recommend
            margin-top: 8px
            font-size: 0
            .icon-thumb_up
              display: inline-block
              vertical-align: top
              font-size: 12px
              line-height: 16px
              color: rgb(0, 160, 220)
            .rcm
              display: inline-block
              vertical-align: top
              margin-left: 8px
              padding: 0 6px
              background: #fff
              font-size: 9px
              line-height: 16px
              color: rgb(147, 153, 159)
              border: 1px solid rgba(7, 17, 27, 0.1)
              border-radius: 2px
        .time1
          position: absolute
          right: 0
          top: 18px
          font-size: 10px
          font-weight: 200
          line-height: 12px
          color: rgb(147, 153, 159)
</style>



