<template>
  <transition name="food">
    <div v-show="showFlag" class="food" ref="food">
      <div class="food-content">
        <div class="img-header">
          <img :src="food.image" class="image">
          <div class="back">
            <i class="icon-close" @click="backGood"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="sell-count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}</span>
          </div>
          <div class="price">
            <span class="now">¥{{food.price}}</span><span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
          </div>
          <div class="cartcontrol-wrapper">
            <cartcontrol @cartadd="addFood" :food="food"></cartcontrol>
          </div>
          <div class="add-food" v-show="!food.count || food.count===0" @click="addFirst">
            <span class="add">加入购物车</span>
          </div>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h1 class="title">商品介绍</h1>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <ratingselect @toggle="toggle" @selecttype="changselect" :ratings="food.ratings" :desc="desc"
                        :selectType="selectType" :onlyContent="onlyContent"></ratingselect>
        </div>
        <div class="rating-wrapper">
          <div v-show="food.ratings&&food.ratings.length>0" class="have-rating">
            <div v-show="showrating(rating.text,rating.rateType)" class="rating" v-for="rating in food.ratings">
              <div class="time">{{rating.rateTime | formatData}}</div>
              <p class="content">
                <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span><span
                class="text">{{rating.text}}</span>
              </p>
              <div class="user">
                <span class="userName">{{rating.username}}</span>
                <img class="avatar" :src="rating.avatar">
              </div>
            </div>
          </div>
          <div v-show="!food.ratings||food.ratings.length===0" class="no-rating">
            <span class="text">暂无评论</span>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import BSroll from 'better-scroll';
  import cartcontrol from 'components/cartcontrol/cartcontrol';
  import {formatData} from 'common/js/data';
  import Vue from 'vue';
  import split from 'components/split/split';
  import ratingselect from 'components/ratingselect/ratingselect';
  //    const POSITIVE = 0;
  //    const NEGATIVE = 1;
  const ALL = 2;
  export default {
    props: {
      food: {
        type: Object
      }
    },
    data () {
      return {
        showFlag: false,
        selectType: ALL,
        onlyContent: true,
        desc: {
          ALL: '全部',
          POSITIVE: '推荐',
          NEGATIVE: '吐槽'
        }
      };
    },
    methods: {
      show () {
        this.showFlag = true;
        this.selectType = 2;
        this.onlyContent = true;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BSroll(this.$refs.food, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      showrating (text, type) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      },
      changselect (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      toggle () {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      backGood () {
        this.showFlag = false;
      },
      addFirst (event) {
        if (!event._constructed) {
          return;
        }
        this.$emit('add', event.target);
        Vue.set(this.food, 'count', 1);
      },
      addFood (target) {
        this.$emit('add', target);
      }
    },
    filters: {
      formatData (time) {
        let date = new Date(time);
        return formatData(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      cartcontrol,
      split,
      ratingselect
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .food
    position: fixed
    left: 0
    top: 0
    bottom: 48px
    z-index: 30
    width: 100%
    background: #fff
    &.food-enter-active, &.food-leave-active
      transition: all 0.2s linear
      transform: translate3d(0, 0, 0)
    &.food-enter, &.food-leave-active
      transform: translate3d(100%, 0, 0)
    .img-header
      position: relative
      width: 100%
      height: 0
      padding-top: 100%
      .image
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%

      .back
        position: absolute
        left: 0
        top: 0
        color: #fff
        font-size: 24px
    .content
      padding: 18px
      position: relative
      .title
        font-size: 14px
        font-weight: 700
        color: rgb(7, 17, 27)
        line-height: 14px
      .detail
        margin-top: 8px
        margin-bottom: 18px
        font-size: 0
        line-height: 10px
        .sell-count, .rating
          font-size: 10px
          color: rgb(147, 153, 159)
        .sell-count
          margin-right: 12px
      .price
        font-weight: 700
        line-height: 24px
        .now
          font-size: 14px
          color: rgb(240, 20, 20)
        .old
          text-decoration: line-through
          margin-left: 8px
          font-size: 10px
          color: rgb(147, 153, 159)
      .cartcontrol-wrapper
        position: absolute
        right: 12px
        bottom: 12px
      .add-food
        position: absolute
        right: 18px
        bottom: 18px
        padding: 0 12px
        box-sizing: border-box
        height: 24px
        line-height: 24px
        background: rgb(0, 160, 220)
        border-radius: 12px
        text-align: center
        .add
          font-size: 10px
          line-height: 12px
          color: #fff
    .info
      padding: 18px
      .title
        line-height: 14px
        margin-bottom: 6px
        font-size: 14px
        color: rgb(7, 17, 27)
      .text
        padding: 0 8px
        line-height: 24px
        font-size: 12px
        font-weight: 200
        color: rgb(77, 85, 93)

    .rating
      padding-top: 18px
      .title
        margin-left: 18px
        line-height: 14px
        font-size: 14px
        color: rgb(7, 17, 27)

    .rating-wrapper
      padding: 0 18px
      .have-rating
        .rating
          position: relative
          padding: 16px 0
          .time
            margin-bottom: 6px
            font-size: 10px
            line-height: 12px
            color: rgb(147, 153, 159)
          .content
            font-size: 0
            .icon-thumb_up, .icon-thumb_down
              margin-right: 4px
              font-size: 12px
              lint-height: 24px
              &.icon-thumb_up
                color: rgb(0, 160, 220)
              &.icon-thumb_down
                color: rgb(147, 153, 159)
            .text
              font-size: 12px
              color: rgb(7, 17, 27)
              line-height: 16px
          .user
            position: absolute
            top: 16px
            right: 0
            font-size: 0
            .userName
              display: inline-block
              vertical-align: top
              margin-right: 6px
              font-size: 10px
              line-height: 12px
              color: rgb(147, 153, 159)
            .avatar
              vertical-align: top
              width: 12px
              height: 12px
              border-radius: 50%
      .no-rating
        padding: 16px 0
        .text
          font-size: 12px
          line-height: 16px
          color: rgb(147, 153, 159)
</style>



