<template>
  <div class="cartcontrol">
    <transition name="move">
      <div class="count-delete" v-show="food.count>0" @click.stop="deletecart">
        <span class="inner in-move icon-remove_circle_outline"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="count-add icon-add_circle" v-show="true" @click.stop="addcart"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  import Vue from 'vue';

  export default {
    props: {
      food: {
        type: Object
      },
      selectFood: {
        type: Array
      }
    },
    methods: {
      addcart (event) {
        if (!event._constructed) {
          return;
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1);
        } else {
          this.food.count++;
        }
        this.$emit('cartadd', event.target);
      },
      deletecart (event) {
        if (!event._constructed) {
          return;
        };
        if (this.food.count) {
          this.food.count--;
        };
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .cartcontrol
    font-size: 0
    .count-delete
      display: inline-block
      padding: 6px
      &.move-enter-active,&.move-leave-active
        transition: all 0.3s linear
        opacity: 1
        transform: translate3d(0, 0, 0)
        .inner
          transition: all 0.3s linear
          transform: rotate(0deg)
      &.move-enter,&.move-leave-to
        opacity: 0
        transform: translate3d(24px,0,0)
        .inner
          transform: rotate(180deg)
      .inner
        display: inline-block
        line-height: 24px
        font-size: 24px
        color: rgb(0, 160, 220)
        transition: all 0.3s
    .cart-count
      display: inline-block
      vertical-align: top
      width: 12px
      padding-top: 6px
      line-height: 24px
      text-align: center
      font-size: 10px
      color: rgb(147, 153, 159)
    .count-add
      display: inline-block
      padding: 6px
      line-height: 24px
      font-size: 24px
      color: rgb(0, 160, 220)
</style>



