<template>
  <div class="seller" ref="sellerWrapper">
    <div class="seller-content">
      <div class="overview">
        <h1 class="title">{{seller.name}}</h1>
        <div class="star-content">
          <star :size="36" :score="seller.score"></star>
          <span class="ratingCount">({{seller.ratingCount}})</span>
          <span class="sellCount">月售{{seller.sellCount}}单</span>
        </div>
        <ul class="remark">
          <li class="block">
            <h2 class="title">起送价</h2>
            <div class="price">
              <span class="price1">{{seller.minPrice}}</span>
              <span class="price2">元</span>
            </div>
          </li>
          <li class="block">
            <h2 class="title">商家配送</h2>
            <div class="price">
              <span class="price1">{{seller.deliveryPrice}}</span>
              <span class="price2">元</span>
            </div>
          </li>
          <li class="block">
            <h2 class="title">平均配送时间</h2>
            <div class="price">
              <span class="price1">{{seller.deliveryTime}}</span>
              <span class="price2">元</span>
            </div>
          </li>
        </ul>
        <div class="favorite">
          <span class="icon-favorite" @click="toggleFav" :class="{'active': showFavorite}"></span>
          <span class="text">{{showFavText}}</span>
        </div>
      </div>
      <split></split>
      <div class="bulletin">
        <div class="maintext">
          <h2 class="title">公告与活动</h2>
          <p class="text">{{seller.bulletin}}</p>
        </div>
        <ul class="supports">
          <li class="supports-item" v-for="(item,index) in seller.supports">
            <span class="icon" :class="classMap[seller.supports[index].type]"></span>
            <span class="text">{{seller.supports[index].description}}</span>
          </li>
        </ul>
      </div>
      <split></split>
      <div class="pics">
        <h2 class="title">商家实景</h2>
        <div class="pic-wrapper" ref="picWrapper">
          <ul class="pic-list" ref="pic">
            <li class="pic-item" v-for="pic in seller.pics">
              <img :src="pic" width="120" height="90">
            </li>
          </ul>
        </div>
      </div>
      <split></split>
      <div class="info">
        <h2 class="title">商家信息</h2>
        <ul>
          <li class="info-item border-1px" v-for="item in seller.infos">{{item}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';
  import split from 'components/split/split';
  import Bscroll from 'better-scroll';
  import { saveToLocal, loadFromLocal } from 'common/js/store.js';

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        showFavorite: (() => {
          loadFromLocal(this.seller.id, 'favorite', false);
        })()
      };
    },
    created () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    watch: {
      'seller' () {
        this.$nextTick(() => {
          this._initScroll();
          this._initPic();
        });
      }
    },
    mounted () {
      this._initScroll();
      this._initPic();
    },
    methods: {
      _initScroll () {
        if (!this.scroll) {
          this.scroll = new Bscroll(this.$refs.sellerWrapper, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      },
      toggleFav (event) {
        if (!event._constructed) {
          return;
        }
        ;
        this.showFavorite = !this.showFavorite;
        saveToLocal(this.seller.id, 'favarite', this.favorite);
      },
      _initPic () {
        if (this.seller.pics) {
          let picWidth = 120;
          let margin = 6;
          let width = (picWidth + margin) * this.seller.pics.length - margin;
          this.$refs.pic.style.width = width + 'px';
          this.$nextTick(() => {
            if (!this.picScroll) {
              this.picScroll = new Bscroll(this.$refs.picWrapper, {
                scrollX: true,
                eventPassthrough: 'vertical'
              });
            } else {
              this.picScroll.refresh();
            }
          });
        }
      }
    },
    computed: {
      showFavText () {
        return this.showFavorite ? '收藏' : '未收藏';
      }
    },
    components: {
      star,
      split
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin.styl';
  .seller
    position: fixed
    top: 174px
    left: 0
    bottom: 0
    width: 100%
    overflow: hidden
    .overview
      position: relative
      padding: 18px
      .title
        font-size: 14px
        line-height: 14px
        color: rgb(7, 17, 27)
      .star-content
        margin: 8px 0 18px 0
        font-size: 0
        .star
          display: inline-block
          vertical-align: top
        .ratingCount
          display: inline-block
          vertical-align: top
          margin: 0 12px 0 8px
          font-size: 10px
          line-height: 18px
          color: rgb(77, 85, 93)
        .sellCount
          display: inline-block
          vertical-align: top
          font-size: 10px
          line-height: 18px
          color: rgb(77, 85, 93)
      .remark
        display: flex
        padding-top: 18px
        border-top: 1px solid rgba(7, 17, 27, 0.1)
        .block
          flex: 1
          &.block:nth-child(2)
            border-right: 1px solid rgba(7, 17, 27, 0.1)
            border-left: 1px solid rgba(7, 17, 27, 0.1)
          .title
            text-align: center
            font-size: 10px
            line-height: 10px
            color: rgb(147, 153, 159)
          .price
            margin-top: 4px
            text-align: center
            font-size: 0
            .price1
              font-size: 24px
              font-weight: 200
              line-height: 24px
              color: rgb(7, 17, 27)
            .price2
              font-size: 10px
      .favorite
        position: absolute
        width: 50px
        right: 18px
        top: 18px
        text-align: center
        .icon-favorite
          display: block
          font-size: 24px
          line-height: 24px
          color: rgb(147, 153, 159)
          &.active
            color: rgb(240, 20, 20)
        .text
          display: block
          font-size: 10px
          line-height: 10px
          color: rgb(77, 85, 93)
    .bulletin
      padding: 18px 18px 0
      .maintext
        padding-bottom: 16px
        .title
          margin-bottom: 8px
          font-size: 14px
          line-height: 14px
          color: rgb(7, 17, 27)
        .text
          padding: 0 12px
          font-size: 12px
          font-weight: 200
          line-height: 24px
          color: rgb(240, 20, 20)
      .supports
        padding: 0 12px
        .supports-item
          padding: 16px 0
          font-size: 0
          .icon
            display: inline-block
            width: 16px
            height: 16px
            vertical-align: top
            background-size: 16px 16px
            background-repeat: no-repeat
            &.decrease
              bg-image(decrease_4)
            &.discount
              bg-image(discount_4)
            &.guarantee
              bg-image(guarantee_4)
            &.invoice
              bg-image(invoice_4)
            &.special
              bg-image(special_4)
          .text
            display: inline-block
            vertical-align: top
            margin-left: 6px
            font-size: 12px
            font-weight: 200
            line-height: 16px
            color: rgb(7, 17, 27)
    .pics
      padding: 18px 0 18px 18px
      .title
        margin-bottom: 12px
        font-size: 14px
        color: rgb(7, 17, 27)
        font-size: 14px
      .pic-wrapper
        width: 100%
        overflow: hidden
        white-space: nowrap
        .pic-list
          font-size: 0
          .pic-item
            display: inline-block
            margin-right: 6px
            width: 120px
            height: 90px
            &:last-child
              margin: 0
    .info
      padding: 0 18px
      .title
        padding: 12px 0
        font-size: 14px
        line-height: 14px
        color: rgb(7, 17, 27)
      .info-item
        padding: 16px 12px
        border-1px(rgba(7, 17, 27, 0.1))
        font-size: 12px
        font-weight: 200
        line-height: 16px
        color: rgb(7, 17, 27)
</style>



